.submit {
  margin-left: .5em;
  font-family: 'Krona One', sans-serif;
  font-size: 0.7rem;
  color: black;
  padding: 15px 35px;
  background: none;
  border: none;
  position: relative;
  text-transform: uppercase;
  cursor: pointer;
}

.submit:after,
.submit:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border: 1px solid black;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.submit:after {
  -webkit-transform: translate(3px, 3px);
  transform: translate(3px, 3px);
}

.submit:before {
  -webkit-transform: translate(-3px, -3px);
  transform: translate(-3px, -3px);
}

.submit:hover:after,
.submit:hover:before {
  -webkit-transform: translate(0);
  transform: translate(0);
}

.draw {
  cursor: pointer;
  font-family: 'Krona One', sans-serif;
  background: none;
  border: 0;
  box-sizing: border-box;
  margin-top: 3em;
  margin-bottom: 3em;
  margin-right: 1em;
  padding: 1em 1em;
  box-shadow: inset 0 0 0 1px #8bd1a0;
  color: #8bd1a0;
  font-size: 0.8rem;
  font-weight: 700;
  position: relative;
  vertical-align: middle;
}

.draw::before,
.draw::after {
  box-sizing: inherit;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
}

.draw {
  transition: color 0.25s;
}

.draw::before,
.draw::after {
  border: 1px solid transparent;
  width: 0;
  height: 0;
}

.draw::before {
  top: 0;
  left: 0;
}

.draw::after {
  bottom: 0;
  right: 0;
}

.draw:hover {
  color: #0071bc;
}

.draw:hover::before,
.draw:hover::after {
  width: 100%;
  height: 100%;
}

.draw:hover::before {
  border-top-color: #0071bc;
  border-right-color: #0071bc;
  transition: width 0.25s ease-out, height 0.25s ease-out 0.25s;
}

.draw:hover::after {
  border-bottom-color: #0071bc;
  border-left-color: #0071bc;
  transition: border-color 0s ease-out 0.5s, width 0.25s ease-out 0.5s,
    height 0.25s ease-out 0.75s;
}


