  overflow: hidden;
  -webkit-animation: movinInBlock infinite ease-out;
          animation: movinInBlock infinite ease-out;
}

.square div {
  position: absolute;
  background: #000;
}

.square .line-top {
  height: 10px;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-animation: lineTop 0.8s cubic-bezier(0.05, 0.79, 0.82, 0.31);
          animation: lineTop 0.8s cubic-bezier(0.05, 0.79, 0.82, 0.31);
}

.square .line-right {
  height: 100%;
  width: 10px;
  top: 0;
