.about-container {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  margin: 0 auto;
  position: relative;
  width: auto;
  height: 100%;
}

.about-container.fluid {
  width: 100%;
  max-width: 100%;
  padding: 0;
}

@media (min-width: 1024px) {
  .about-container {
    max-width: 960px;
  }
}

@media (min-width: 1216px) {
  .about-container {
    max-width: 1152px;
  }
}

@media (max-width: 1408px) {
  .about-container {
    max-width: 1560px;
  }
  .single .about-container .bottom-row .bottom .thumbnail-single .frame-single img {

    width: 80%;
  }
}

.about-container .row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.about-container .row.space-between {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.about-container .row.center {
  -webkit-justify-content: center;
          justify-content: center;
}

header {
  font-size: 16px;
  position: fixed;
  z-index: 99;
  width: 100%;
  font-weight: "700";
}

header .about-container .row {
  height: 128px;
}

header .about-container .row .logo a {
  color: #1e1f13;
  text-decoration: none;
}

header .about-container .row .menu {
  cursor: pointer;
  border-radius: 100%;
  border: 1px solid #1e1f13;
  height: 80px;
  width: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  transition: 0.3s ease-in-out;
}

header .about-container .row .menu:hover {
  color: #ffffff;
  background: #1e1f13;
}

main .about-container {
  position: relative;
}

main .about-container .row {
  -webkit-align-items: center;
          align-items: center;
}

main .about-container .row .image-container {
  position: relative;
}

main .about-container .row .image-container .thumbnail {
  overflow: hidden;
  position: relative;
  text-align: center;
  margin-top: 6em;
}

main .about-container .row .image-container .thumbnail .frame img{
  width: 424px;
}

main .about-container .row .image-container .information {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 1em;
  margin-bottom: 1em;
  text-transform: uppercase;
}

main .about-container .row .image-container .information .location span:nth-child(2) {
  margin-left: 8px;
}

.single .about-container .top-row {
  height: 50vh;
  width: 100%;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.single .about-container .top-row .top {
  padding-bottom: 40px;
}

.single .about-container .top-row .top .details {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.single .about-container .top-row .top .details .location span:nth-child(2) {
  margin-left: 16px;
}

.single .about-container .top-row .top .details .mua {
  text-transform: uppercase;
}

.single .about-container .top-row .top .model {
  overflow: hidden;
}

.single .about-container .top-row .top .model .first {
  margin-right: 72px;
}

.single .about-container .top-row .top .model span {
  display: inline-block;
  position: relative;
  font-weight: 400;
  font-size: 60px;
}

@media (max-width: 1440px) {
  .single .about-container .top-row .top .model span {
    font-size: 60px;
  }
}

.single .about-container .bottom-row {
  height: 50vh;
  position: relative;
}

.single .about-container .bottom-row .bottom {
  height: 100%;
  width: 100%;
}

.single .about-container .bottom-row .bottom .thumbnail-single {
  width: 100%;
  height: 800px;
  margin: 0 auto;
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
}

.single .about-container .bottom-row .bottom .thumbnail-single .frame-single img {

  width: 100%;
}

.single .about-container .bottom-row .scroll-for-more {
  position: absolute;
  bottom: 200px;
  left: 200px;
  z-index: 1;
}

.single .about-container .bottom-row .scroll-for-more .icon svg {
  height: auto;
  width: 28px;
}

.single .about-container .bottom-row .scroll-for-more .text {
  margin-top: 8px;
  color: #ffffff;
  text-transform: uppercase;
}

.detailed-information {
  margin-top: 230px;
  margin-bottom: 100px;
}

.detailed-information .about-container .row {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.detailed-information .about-container .row h2 {
  font-size: 28px;
}

.detailed-information .about-container .row p {
  font-size: 16;
  line-height: 28px;
  font-weight: 400;
  width: 800px;
}

@media only screen and (max-width: 978px) {
  .detailed-information {
    margin-top: 70px;
    margin-bottom: 50px;
  }

  .single .about-container .bottom-row .bottom .thumbnail-single .frame-single img {
    width: 130%;
  }
}

@media only screen and (max-width: 700px) {

  main .about-container .row .image-container .thumbnail .frame img {
    width: 90%;
  }



  .single .about-container .bottom-row .bottom .thumbnail-single .frame-single img {
    width: 130%;
  }

  .single .about-container .bottom-row {
    height: 30vh;
    position: relative;
  }

  .detailed-information .about-container .row h2 {
    font-size: 20px;
  }

  .detailed-information .about-container .row p {
    font-size: 11px;
  }

  .about-text-two {
    display: none;
  }

  .single .about-container .top-row .top .details {
    display: block;
  }

  .single .about-container .top-row .top .model span {
    font-size: 50px;
  }

  main .about-container .row .image-container .information {
    font-size: 15px;
  }

  .detailed-information {
    margin-top: 140px;
    margin-bottom: 50px;
  }

  .single .about-container .top-row {
    height: 35vh;
    width: 100%;
  }
}

@media only screen and (max-width: 500px) {
  .single .about-container .bottom-row .bottom .thumbnail-single .frame-single img {

    width: 220%;
  }

  .single .about-container .bottom-row .scroll-for-more {
    bottom: 130px;
    left: 200px;
}

  .single .about-container .top-row .top {
    padding-top: 100px;
    padding-bottom: 40px;
}

  .single .about-container .top-row .top .model span {
    font-size: 40px;
  }

  main .about-container .row .image-container .thumbnail .frame img {
    margin-top: 3em;
  }

  .about-container .row {
    display: block;
    -webkit-box-align: center;
  }

  .detailed-information .about-container .row p {

    width: 100%;
  }
}
